import React from "react";
import ListItem from "../ListItem/ListItem";
import axios from "axios";
import "./Wiki.module.css";

let time;
window.id = 0;

class Wiki extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      searchItem: "Programming",
      items: [],
      current_item: {
        content: "",
        link: "",
        key: "",
      },
    };

    this.getContent = this.getContent.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.resetTimer = this.resetTimer.bind(this);
    this.setValues = this.setValues.bind(this);
  }

  handleInput(event) {
    this.setState({ searchItem: event.target.value });
  }

  resetTimer() {
    clearTimeout(time);
    time = setTimeout(this.getContent, 500);
  }

  componentDidMount() {
    this.getContent();
  }

  setValues(response) {
    this.setState({ items: [] });
    for (let i = 0; i < response[1].length; i++) {
      this.setState((prevState) => {
        return {
          items: prevState.items.concat({
            content: response[1][i],
            link: response[3][i],
            key: window.id++,
          }),
        };
      });
    }
  }

  getContent() {
    axios
      .get("https://en.wikipedia.org/w/api.php", {
        params: {
          action: "opensearch",
          origin: "*",
          search: this.state.searchItem,
        },
      })
      .then((response) => this.setValues(response.data));
  }

  render() {
    return (
      <div>
        <h1> Wiki-Search</h1>
        <input
          type="text"
          value={this.state.searchItem}
          onChange={this.handleInput}
          onKeyUp={this.resetTimer}
        />
        <p>*Note: Search for any content and wait for 500ms to view results (hyperlink)</p>
        <br />
        <ListItem items={this.state.items} />
      </div>
    );
  }
}

export default Wiki;
